import styled from "styled-components"

const Underlining = styled.span`
box-shadow: inset 0 ${({ big }) => (big ? "-0.16rem" : "-0.16rem")} 0
    ${({ theme, highlight }) =>
      highlight ? theme.colors.secondary : theme.colors.background};
  transition: box-shadow 0.3s ease-out;
  &:hover {
    box-shadow: inset 0 ${({ big }) => (big ? "-2.0rem" : "-1.16rem")} 0
      ${({ theme }) => theme.colors.secondary};
  }
`

export default Underlining
